.dropbtn {
    /* background-color: #04AA6D; */
    color: white;
    /* padding: 16px; */
    /* font-size: 16px; */
    border: none;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #08487E;
    font-weight: 600;
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: white;
    padding: 5px 9px;
    font-size: 14px;
    text-decoration: none;
    display: block;
   
  }
  
  .dropdown-content a:hover {background-color: #0B60A9;}
  
  .dropdown:hover .dropdown-content {display: block;}
  
