.singleserlist{
    font-size: 16px;
    border-bottom: 1px solid lightgray;
    padding: 10px 0;
    text-decoration: none;
    list-style: none;
    transition: all ease 0.5s;
}
.singleserlist:hover{
    color: cornflowerblue;
    padding-left: 20px;
    cursor: pointer;
}
.singleimg{
    height: 417px;
    width: 870px;
    max-width: 100%;
}