
  /* From Uiverse.io by sohoning */ 
.cards {
    position: relative;
    height: 300px;
    width: 100%;
    transition-duration: 0.5s;
    background: none;
    overflow: hidden;
  }
  
  .cards:hover {
    height: 420px;
  }
  
  /* .cards:hover .outlinePage {
    box-shadow: 0 10px 15px #b1985e;
  } */
  
  .cards:hover .detailPage {
    display: flex;
    background-color: #08487E;
  }
  
  .outlinePage {
    position: relative;
    /* background: linear-gradient(45deg, #fffbf0, #ffdd87); */
    width: 300px;
    height: 300px;
    border-radius: 25px;
    transition-duration: 0.5s;
    z-index: 2;
  }
  
  .detailPage {
    position: relative;
    display: none;
    width: 300px;
    height: 270px;
    background: white;
    margin-top: 20px;
    top: -50px;
    z-index: 1;
    transition-duration: 1s;
    border-radius: 0 0 25px 25px;
    overflow: hidden;
    /* align-items: center; */
    justify-content: flex-start;
  }
  
  .splitLine {
    position: absolute;
    width: 200px;
    height: 10px;
    top: 100px;
    background-image: linear-gradient(
      to right,
      transparent 10%,
      #ffe8a0 20%,
      #f7b733 50%,
      #ffe8a0 70%,
      transparent 90%
    );
    z-index: 1;
  }
  
  .trophy {
    position: absolute;
    right: 0px;
    top: 4px;
    z-index: 2;
  }
  
  .ranking_number {
    position: relative;
    color: #ffc64b;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
    font-size: 80px;
    left: 20px;
    padding: 0;
    margin: 0;
    top: -5px;
  }
  
  .ranking_word {
    position: relative;
    font-size: 40px;
    color: #424c50;
  }
  
  .userAvatar {
    position: absolute;
    bottom: 6px;
    left: 20px;
  }
  
  .userName {
    position: relative;
    font-weight: 600;
    color: #6b7578;
    left: 55px;
    font-size: 18px;
    top: -2px;
  }
  
  .medals {
    position: absolute;
    top: 15px;
    right: 5px;
  }
  
  .gradesBox {
    position: relative;
    /* height: 75px; */
    top: 10px;
    margin-right: 10px;
    margin-left: 15px;
  }
  
  .gradesIcon {
    position: absolute;
    top: 10px;
  }
  
  .gradesBoxLabel {
    position: relative;
    display: block;
    margin-left: 10px;
    color: white;
    letter-spacing: 6px;
    font-family: Arial, Helvetica, sans-serif;
    /* margin-top: 20px; */
    font-weight: 600;
    font-size: 13px;
  }
  
  .gradesBoxNum {
    position: relative;
    font-family: Arial, Helvetica, sans-serif;
    display: block;
    padding-top: 10px;
    font-size: 20px;
    font-weight: 800;
    /* margin-left: 60px; */
    color: #ea9518;
    top: 5px;
  }
  
  .timeNum {
    color: #6cabf6;
  }
  
  .slide-in-top {
    animation: slide-in-top 1s cubic-bezier(0.65, 0.05, 0.36, 1) both;
  }
  
  @keyframes slide-in-top {
    0% {
      transform: translateY(-100px);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
  