/* src/components/home.css */
.carousel-container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.carousel-slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel-background {
  padding: 100px 300px 100px 100px;
  width: 100%;
  height: 80vh; /* Adjust as needed */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}

.carousel-head {
  color: white;
  font-size: 2.5em;
  font-weight: 700;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-align: left;
  /* text-decoration: underline; */
}
.carousel-text {
  color: white;
  font-size: 1.5em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  text-align: left;
}

.slick-prev:before,
.slick-next:before {
  color: black; /* Adjust arrow color as needed */
}

@media (max-width: 1024px) {
  .carousel-background {
    height: 70vh;
    padding: 100px; /* Adjust height for medium screens */
  }

  .carousel-text {
    font-size: 1.75em; /* Adjust text size for medium screens */
  }
}

@media (max-width: 768px) {
  .carousel-background {
    height: 70vh;
    padding: 50px; /* Adjust height for small screens */
  }

  .carousel-head {
    font-size: 1.5em; /* Adjust text size for small screens */
  }
  .carousel-text {
    font-size: 1em; /* Adjust text size for small screens */
  }
}

@media (max-width: 480px) {
  .carousel-background {
    height: 70vh; /* Adjust height for extra small screens */
  }

  .carousel-head {
    font-size: 1.5em; /* Adjust text size for extra small screens */
  }
  .carousel-text {
    font-size: 1em; /* Adjust text size for extra small screens */
  }
}

/* src/pages/CardSlider.css */
.card-slider-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px;
  overflow: hidden;
  position: relative;
}

.card-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  height: 345px;
  width: 100%;
  max-width: 550px;
  background: transparent;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s;
  /* box-shadow: px 0px 5px rgb(155, 153, 153); */
}



 .card-image {
  width: 100%;
  height: 345px;
  object-fit: cover;
} 

.card-content {
  padding: 0px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.68);
  color: white;
}

.card-content h3 {
  margin: 0 0 10px;
  font-size: 1.5em;
  color: white;
}

.card-content p {
  margin: 0;
  font-size: 1em;
  color: white;
}

.slick-prev:before,
.slick-next:before {
  color: white;
}

.homeslider2{
  background-image: url("../img/services-bg.jpg");
  padding: 40px 0px;
